@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
.instruction-main-div {
  padding: 20px 50px;
  height: 100vh;

  .main-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    .left-content {
      .instructions-heading .MuiTypography-body1 {
        font-size: 40px;
        color: #04724d;
        font-weight: bold;
        /* font-family: Ubuntu; */
        line-height: 1.2;
      }
      .instructions-subHeading .MuiTypography-body1 {
        font-size: 20px;
        color: #000000;
        opacity: 60%;
      }
    }
    .right-content {
      .instructions-logo {
        width: 200px;
        img {
          width: 100%;
        }
      }
    }
  }
  .page-content {
    display: flex;
    justify-content: center;
    .contents-verbal {
      padding: 25px;
      border-radius: 4px;
      background-color: #ffffff;
      width: 30%;
      position: absolute;
      top: 35%;

      .li-2 {
        font-size: 16px;
        margin-bottom: 10px;
        color: #000000 !important;
        opacity: 1;
        font-weight: 600;
        display: flex;
        align-items: center;
      }

      .dot {
        height: 5px;
        width: 5px;
        background: #000;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
  }
  .continue-button-wrapper {
    position: absolute;
    bottom: 10%;
    right: 10%;
    .continue-button {
      width: 150px;
      cursor: pointer;
      font-size: 16px;
      background-color: #04724d;
      color: #ffffff;
      width: 150px;
      border-radius: 8px;
    }
  }
}
