}

.interview-video-new {
  position: relative;
  height: 100%;
  width: 630px;
}
.interview-video-new video {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
}
.controls {
  display: none;
}
.candidateName {
  font: normal normal 600 29px/43px Ubantu;
  position: absolute;
  top: 0px;
  left: 25px;
  margin-top: 10px;
  opacity: 1;
}
.candidateName span {
  margin-left: 10px;
}
.question-div {
  width: 100%;
