}
.timing {
  position: absolute;
  bottom: 0px;
  font-size: 28px;
  font-weight: bold;
  right: 0px;
  margin-right: 15px;
  letter-spacing: 0px;
  color: #f4f9f5;
  margin-bottom: 30px;
}
.step-label {
  color: black;
}
.circle-interview {
  /* position: absolute; */
  text-align: center;
  /* right: 0; */
  margin-left: auto;
  height: 40px;
  width: 40px;
  font-size: 20px;
  padding: 5px;
  background-color: #cadedb;
  border-radius: 50%;
  float:right
}
.questions-wrapper{
  width: 180px;
  display: flex;
  border: 1px solid gray;
  border-radius: 27px;
  padding-left: 10px;
  background: white;
  align-items: center;
}

.total{
  font-size: 13px;
}
.previews-heading .MuiTypography-body1 {
  font-size: 34px;
  font-weight: bolder;
