  padding: 40px 10px 10px 10px;
}
.skipnextButton {
  display: flex;
  justify-content: space-evenly;
}

.qustion-content-div {
  padding: 20px 0;
}

.question {
  font-size: 26px;
  line-height: 40px;
  font-weight: bold;
  text-align: left;
}

.question-div .MuiPaper-root {
  background: transparent;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.question-div .MuiStepLabel-label.MuiStepLabel-alternativeLabel {
  display: none;
}

.question-div .MuiStepConnector-alternativeLabel {
  display: none;
}

.question-div .MuiSvgIcon-root {
  width: 2em;
  height: 2em;
}

.skipButton,
.nextButton {
  width: 43%;
  margin: 0 20px;
}

.skipButton .MuiButton-contained {
  background: transparent;
  color: #04724d;
  padding: 12px;
  width: 100%;
  font-size: 18px;
  border: 2px solid#04724D;
  font-weight: bold;
  border-radius: 20px;
}

.nextButton .MuiButton-contained {
  border-radius: 20px;
  background-color: #04724d;
  border: 1px solid#04724D;
  color: #ffffff;
  padding: 5px 12px 5px 12px;
  font-size: 18px;
  width: 100%;
}
.timingCounting {
  background-color: #ffffff;
  width: 20%;
  border-radius: 25px;
  position: absolute;
  left: 0px;
  bottom: 0px;
  text-align: center;
  padding: 10px 0px;
  margin: 0px 0px 20px 15px;
  color: #04724d;
  font-size: 20px;
  font-weight: bold;
}
.alarmIcon {
  margin-right: 10px;
}

.nextButton .MuiButton-contained:hover {
  background-color: #04724d;
}

.question-div .MuiStepIcon-root.MuiStepIcon-active {
  color: #04724d;
}

.question-div .MuiStepIcon-root.MuiStepIcon-completed {
  cursor: pointer;
  color: #04724d;
}
.question-div .MuiStepIcon-root {
  color: #04724d1a;
}

.Button {
  background-color: #04724d;
  color: #ffffff;
}

.buttons-wrapper{
  display: flex;
  align-items: center;
  margin-left: 20px;

}

