@media(max-width:1024px){
  .total{
    font-size: 11px;
  }
  .interview-main-div{
    padding: 0px 20px;
    background: #fff;
    height: 100vh;
  }
  .total-questions{
    display: none;
  }
  .total-questions-new{

    padding: 20px 0;
    position: fixed;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);

  }
  .inter-1{
    width: 100%;
  }
  
  .inter-2{
    width: 0%;
  }
  .interview-row-wrapper{
   flex-direction: column;
   margin-bottom: 0;
  }
  .interview-video-new{
    background: #fff;
  }
  .interview-video-new video{
    height: 400px !important;
    border-radius: 0;
    width: 768px;
    object-fit: cover;
object-position: center
    

  }
  .left-interview{
    width: 100%;
  }
  
  .right-interview{
    width: 100%;
  }

  .questions-wrapper{
    width: 135px;
    /* padding: 5px; */
  }
  .circle-interview{
    width: 35px;
    height: 35px;
    font-size: 16px;
    
  }

  .timer-1{
    display: none !important;

  }

  .timer-2{
    display: block !important;
    margin: 0 15px;
 

  }

  .interview-logo{
    display: none !important;
  }
  .content-div{
    padding: 10px ;
  }
  .qustion-content-div{
  padding: 0px;
  }

  .laptop-only{
    display: none !important;
  }

  .question{
    font-size: 15px;
    line-height: 20px;
  }
}

@media(max-width:765px){
  .interview-video-new video{
    border-radius: 0;
    width: 360px;
    object-fit: cover;
    object-position: center
  }
}

